            * {
                margin: 0px;
                padding: 0px;
                box-sizing: border-box;
            }
            
            html,
            body,
            #root,
            .wrapper {
                padding: 0px;
                margin: 0px;
                height: 100%;
                width: 100%;
                font-family: Georgia, "Times New Roman", Times, serif;
            }
            
            .nav-tabs {
                border-bottom: 0px;
            }
            
            .nav-link {
                padding: 0px!important;
                height: 60px;
                width: 100px;
                font-size: 22px;
                text-align: center;
                line-height: 60px;
            }
            
            .HomeBackground {
                background-image: url("../images/codingdesk.jpg");
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
                overflow: hidden;
            }
            
            h1 {
                font-size: 24px;
                color: white!important;
                padding: 20px;
                margin: 0px;
            }
            /* h2 heading "About Me" attributes*/
            
            h2 {
                font-size: 24px;
                color: #6c757d;
                border-bottom: rgb(173, 171, 171);
                border-bottom-style: solid;
            }
            
            .my-name,
            .myName {
                font-family: 'Courgette';
                font-size: 100px!important;
                padding: 11px;
                margin: 0px;
                border: 0px;
                color: cyan;
                width: 200px;
                text-align: center;
                overflow: auto;
            }
            
            .my-name {
                background-color: darkgrey;
                height: 60px;
                font-size: 30px!important;
            }
            
            .myName {
                font-size: 60px;
                padding: 15px;
                margin: auto;
                display: block;
                padding-bottom: 5px;
            }
            
            .welcome {
                color: cyan;
                font-family: 'Courgette';
                font-size: 40px;
                text-align: center;
                margin-top: 5%;
                margin-bottom: 2%;
            }
            
            .landing {
                position: relative;
                width: 100%;
                height: 100%;
                top: 0px;
                bottom: 0px;
                background: rgba(12, 12, 12, 0.5);
                padding-top: 7%;
            }
            /*h3 inndex content attributes */
            
            h3 {
                font-size: 16px;
                padding-bottom: 10px;
            }
            /* Paragraphs text attributes*/
            
            li>p,
            .resume {
                font-size: 16px;
                text-align: justify;
                text-justify: inter-word;
                margin-bottom: 0px!important;
            }
            
            picture .card p,
            picture .card a,
            picture .card i {
                float: left;
                margin: 0px;
                line-height: 150%;
                margin-left: 5px;
                font-size: 16px;
                height: 150%;
                width: auto;
            }
            /*'main'tag box content attribute*/
            
            main {
                padding: 15px;
            }
            /*Link selector attributes*/
            
            .nav a {
                color: white;
            }
            
            .nav a:hover {
                color: cyan;
            }
            
            picture {
                width: 250px;
                float: left;
                margin: 0 15px 5px 0;
                border: 1px solid rgb(173, 171, 171);
                padding: 5px;
            }
            /* Profil picture */
            
            .profile_image {
                display: block;
                margin: 10px auto 10px auto;
                width: 200px;
                height: auto;
                border-radius: 50%;
            }
            
            .aboutMe {
                max-width: 1070px;
                min-width: 280px;
            }
            
            .aboutMe>p {
                text-align: justify;
            }
            
            .hTwo {
                min-width: 320px;
                padding: 0px;
            }
            
            .card {
                padding: 8px;
                margin-top: 20px;
                background-color: rgb(223, 222, 222);
                border-radius: 8px;
            }
            
            .portfolio {
                padding: 0px;
                margin: 0px;
            }
            
            .portfolio>.card {
                width: 500px!important;
                min-width: 300px;
            }
            
            .summary {
                height: 78px;
            }
            
            .summary p {
                height: auto;
                margin: auto;
                text-align: justify;
            }
            
            .card .card-body {
                height: fit-content;
            }
            
            .card-header {
                margin: auto;
                height: 40px;
            }
            /*contact page button*/
            
            .btn {
                background-color: rgb(33, 119, 99);
                color: white;
            }
            /*contact page form*/
            
            form {
                color: rgb(71, 69, 69);
            }
            
            form {
                color: rgb(71, 69, 69);
            }
            /* Gif zoom on screens that are 620px or more, set the background color to olive */
            
            @media screen and (min-width: 620px) {
                .card .card-body:nth-child(4):hover {
                    transform: scale(1.25);
                }
                .card:hover {
                    z-index: 2;
                }
            }
            /* On screens that are 620px or less, set the background color to olive */
            
            @media screen and (max-width: 620px) {
                .my-name {
                    width: 100%;
                }
                .nav-item {
                    width: 25%!important;
                }
                .nav-link {
                    width: 100%;
                }
            }
            
            @media screen and (max-width: 530px) {
                .nav-item {
                    min-width: 75px;
                    height: 40px;
                }
                .nav-link {
                    height: 40px;
                    font-size: 16px;
                    line-height: 40px;
                }
                h2 {
                    padding: 0px;
                }
                .card-header {
                    height: 30px;
                    padding: 5px!important;
                }
                .portfolio .card {
                    padding: 0px;
                    width: 100%;
                    min-width: 290px;
                }
                .hTwo>h2 {
                    font-size: 18px;
                }
                .summary p {
                    font-size: 13px;
                }
                .card-footer {
                    height: 45px;
                }
                li,
                li>p {
                    font-size: 14px;
                    width: 100%;
                    float: left;
                    margin: 0px;
                }
                .aboutMe>p {
                    font-size: 14px;
                }
            }
            
            @media screen and (max-width: 400px) {
                picture {
                    width: 100%;
                }
                .profile_image {
                    width: 175px;
                }
            }